.article {
margin-top: 100px;
margin-bottom: 46px;

&__area {
    font-family: "Open Sans";
    font-size: 18px;
    line-height: 150%;
    color: $main-blue;
    padding: 24px 53px 14px;
    background: $white;
    border: 1px solid #E5EBF0;
    box-sizing: border-box;
}

&__area h3 {
font-weight: 800;
font-size: 24px;
line-height: 34px;
padding-bottom: 20px;

}

&__area p {
    margin-bottom: 20px;
}
}

@media screen and(max-width: 414px) {
    .article {
        margin-top: 40px;
    }
}